/* Base styles and variables */
:root {
  --background: #191919;
  --secondary-bg: #24242b;
  --text: #fff;
  --accent: #c778dd;
  --secondary-text: #abb2bf;
  --border: #565656;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Fira Code', monospace;
  background: #191919 !important;
  color: var(--text);
  line-height: 1.6;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: var(--text);
}

ul {
  list-style: none;
}

.accent {
  color: var(--accent);
}

.bold {
  font-weight: 700;
}

/* Button styles */
.btn-primary {
  background: transparent;
  color: var(--text);
  border: 1px solid var(--accent);
  padding: 8px 16px;
  cursor: pointer;
  font-family: 'Fira Code', monospace;
  transition: background-color 0.3s;
}

.btn-primary:hover {
  background-color: rgba(199, 120, 221, 0.2);
}

.btn-outline {
  background: transparent;
  color: var(--text);
  border: 1px solid var(--text);
  padding: 8px 16px;
  cursor: pointer;
  font-family: 'Fira Code', monospace;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.btn-outline:hover {
  border-color: var(--accent);
  color: var(--accent);
}

/* Header styles */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid var(--border);
}

.logo {
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 8px;
}

nav ul {
  display: flex;
  gap: 32px;
}

nav ul li a {
  transition: color 0.3s;
}

nav ul li a:hover,
nav ul li a.active {
  color: var(--accent);
}

.language-selector {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

/* Social links */
.social-links {
  position: fixed;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.social-icon {
  font-size: 20px;
  transition: color 0.3s;
}

.social-icon:hover {
  color: var(--accent);
}

/* Hero section */
.hero {
  display: flex;
  align-items: center;
  padding: 80px 0 40px;
  position: relative;
}

.hero-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.hero-text h1 {
  font-size: 32px;
  margin-bottom: 32px;
  line-height: 1.2;
}

.hero-description {
  color: var(--secondary-text);
  margin-bottom: 32px;
}

.hero-image {
  position: relative;
}

.hero-image img {
  max-width: 100%;
  height: auto;
  display: block;
}

.status-badge {
  position: absolute;
  bottom: 16px;
  left: 0;
  background-color: var(--background);
  border: 1px solid var(--accent);
  padding: 8px 16px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.status-dot {
  width: 12px;
  height: 12px;
  background-color: var(--accent);
  border-radius: 50%;
}

/* Geometric elements */
.geometric-element {
  position: absolute;
  pointer-events: none;
}

.rect-1 {
  width: 160px;
  height: 100px;
  /* border: 1px solid var(--accent); */
  top: 94px;
  left: -175px;
}

.dot-pattern-1 {
  width: 100px;
  height: 100px;
  /* background-image: radial-gradient(var(--accent) 1px, transparent 1px); */
  background-size: 10px 10px;
  bottom: 20px;
  right: -40px;
}

/* Quote section */
.quote {
  padding: 80px 0;
  display: flex;
  justify-content: center;
}

.quote-content {
  max-width: 600px;
  position: relative;
  text-align: center;
  border: 1px solid var(--border);
  padding: 32px;
}

.quote-mark {
  font-size: 40px;
  color: var(--accent);
  position: absolute;
}

.quote-mark.left {
  top: 16px;
  left: 16px;
}

.quote-mark.right {
  bottom: 16px;
  right: 16px;
}

.quote-text {
  font-size: 24px;
  margin-bottom: 16px;
}

.quote-author {
  text-align: right;
}

/* Section headers */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 48px;
  border-bottom: 1px solid var(--accent);
  padding-bottom: 16px;
}

.section-header h2 {
  font-size: 32px;
}

.view-all {
  display: flex;
  align-items: center;
  gap: 8px;
  transition: color 0.3s;
}

.view-all:hover {
  color: var(--accent);
}

.arrow {
  font-size: 16px;
}

.double-arrow {
  font-size: 16px;
}

/* Projects section */
.projects {
  padding: 80px 0;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}

.project-card {
  border: 1px solid var(--border);
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-tech {
  border-top: 1px solid var(--border);
  border-bottom: 1px solid var(--border);
  padding: 8px;
  color: var(--secondary-text);
  font-size: 14px;
}

.project-info {
  padding: 16px;
}

.project-info h3 {
  margin-bottom: 16px;
}

.project-links {
  display: flex;
  gap: 16px;
  margin-top: 16px;
}

/* Skills section */
.skills {
  padding: 80px 0;
  position: relative;
}

.skills-content {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.geometric-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.dot-pattern-2,
.dot-pattern-3 {
  position: absolute;
  width: 80px;
  height: 80px;
  background-image: radial-gradient(var(--accent) 1px, transparent 1px);
  background-size: 10px 10px;
}

.dot-pattern-2 {
  top: 0;
  left: 0;
}

.dot-pattern-3 {
  bottom: 100px;
  right: 300px;
}

.square-element,
.square-element-2 {
  position: absolute;
  width: 40px;
  height: 40px;
  border: 1px solid var(--accent);
}

.square-element {
  top: 40px;
  left: 200px;
}

.square-element-2 {
  bottom: 40px;
  right: 100px;
}

.rect-element {
  position: absolute;
  width: 60px;
  height: 60px;
  border: 1px solid var(--accent);
  left: 80px;
  bottom: 0;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
  width: 100%;
}

.skill-category {
  border: 1px solid var(--border);
  padding: 16px;
}

.skill-category h3 {
  border-bottom: 1px solid var(--border);
  padding-bottom: 8px;
  margin-bottom: 16px;
}

.skill-category ul li {
  color: var(--secondary-text);
  margin-bottom: 8px;
}

/* About Me section */
.about-me {
  padding: 80px 0;
}

.about-content {
  display: flex;
  gap: 64px;
}

.about-text {
  flex: 1;
}

.about-text p {
  margin-bottom: 24px;
}

.about-image {
  flex: 1;
  position: relative;
}

.about-image img {
  width: 100%;
  max-width: 300px;
  display: block;
}

.dot-pattern-4 {
  position: absolute;
  width: 100px;
  height: 100px;
  background-image: radial-gradient(var(--accent) 1px, transparent 1px);
  background-size: 10px 10px;
  right: 0;
  bottom: 20px;
}

/* Contacts section */
.contacts {
  padding: 80px 0;
  position: relative;
}

.contacts-content {
  display: flex;
  gap: 64px;
}

.contacts-text {
  flex: 1;
}

.contact-box {
  border: 1px solid var(--border);
  padding: 16px;
  width: 300px;
}

.contact-box h3 {
  margin-bottom: 16px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
}

.dot-pattern-5 {
  position: absolute;
  width: 80px;
  height: 80px;
  background-image: radial-gradient(var(--accent) 1px, transparent 1px);
  background-size: 10px 10px;
  left: 20px;
  bottom: 20px;
}

/* Footer styles */
.footer {
  padding: 40px 0 20px;
  border-top: 1px solid var(--border);
}

.footer-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.footer-logo {
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
}

.footer-title {
  margin-top: 16px;
}

.footer-right h3 {
  margin-bottom: 16px;
}

.footer-links {
  display: flex;
  gap: 16px;
}

.footer-links a {
  font-size: 20px;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: var(--accent);
}

.copyright {
  text-align: center;
  color: var(--secondary-text);
  font-size: 14px;
  padding-top: 20px;
  border-top: 1px solid var(--border);
}

/* Media queries */
@media (max-width: 992px) {
  .hero-content {
    flex-direction: column;
    gap: 40px;
  }

  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .about-content,
  .contacts-content {
    flex-direction: column;
  }

  .social-links {
    position: static;
    flex-direction: row;
    margin-bottom: 40px;
    transform: none;
  }
}

@media (max-width: 576px) {
  .projects-grid,
  .skills-grid {
    grid-template-columns: 1fr;
  }

  .footer-content {
    flex-direction: column;
    gap: 32px;
  }

  nav ul {
    gap: 16px;
  }
}